import React from 'react';

import Questions from './Questions';
import FooterConnectForm from '../FooterConnectForm';
import { useDispatch } from 'react-redux';
import { toggleModalPolicy } from '../../redux/slices/modalSlices';

function Footer() {
   const dispatch = useDispatch();

   const openModalPolicy = () => {
      dispatch(toggleModalPolicy(true));
   };

   const [visibilityForm, setVisibilityForm] = React.useState(true);

   return (
      <div className="footer">
         <Questions />

         <div className="footer-info">
            <div className="footer-info-container">
               <div className="footer-info__columns">
                  <div className="footer-info__col">
                     <h3 className="footer-info__col-title">
                        <span>9</span> 
                     </h3>
                     <p>    </p>
                  </div>
                  <div className="footer-info__col">
                     <h3 className="footer-info__col-title">
                        <span>89</span> 
                     </h3>
                     <p>      </p>
                  </div>
                  <div className="footer-info__col">
                     <h3 className="footer-info__col-title">
                        <span>468470</span>
                     </h3>
                     <p>,    </p>
                  </div>
               </div>
            </div>
         </div>

         <div className="connect">
            <div className="connect-container">
               <div className="connect-top">
                  <h2 className="connect-title"> ,   </h2>
                  <p className="connect-text">
                            ,     
                     .
                  </p>
                  <div className="connect__actions">
                     <div className="connect__actions-col">
                        <h2>01.</h2>
                        <h3></h3>
                        <p className="connect-text">
                                    
                        </p>
                     </div>
                     <div className="connect__actions-col">
                        <h2>02.</h2>
                        <h3> </h3>
                        <p className="connect-text">
                                   
                        </p>
                     </div>
                     <div className="connect__actions-col">
                        <h2>03.</h2>
                        <h3></h3>
                        <p className="connect-text">
                                    
                        </p>
                     </div>
                  </div>
               </div>

               <div className="connect-bot">
                  <h2 className="connect-title">   ?</h2>
                  <p className="connect-text">
                                
                        .     
                          .
                  </p>
                  <div className="connect__buttons">
                     <button className={`connect__button ${!visibilityForm && `but--active`}`} onClick={()=>setVisibilityForm(false)}> </button>
                     <button className={`connect__button ${visibilityForm && `but--active`}`} onClick={()=>setVisibilityForm(true)}> </button>
                  </div>
               </div>

               {visibilityForm ? (
                  <div className="form-wrapper">
                     <FooterConnectForm />
                  </div>
               ) : (
                  <div className="connect__call">
                     <h2>,      </h2>
                     <p>8 (800) 551-90-53</p>
                  </div>
               )}

            </div>
         </div>

         <div className="footer-bot">
            <div className="footer-bot-container">
               <div className="footer-bot__columns">
                  <button onClick={openModalPolicy}> </button>
                  <button> </button>
                  <button> </button>
               </div>
               <p className="footer-bot__text">
                   2021 tvoi-provider.ru -       
                  (: 5674289399393942 / : 92929376467232).   
                           .
                    tvoi-provider.ru,       
                     tvoi-provider.ru ( cookies    ),
                          .
                           
                  Google Analytics, .  .,    
                    tvoi-provider.ru.  ,  {' '}
                  <button onClick={openModalPolicy}> </button>  
                       
               </p>
               <p className="footer-bot__map">
                  <a href="/"> </a>
               </p>
            </div>
         </div>
      </div>
   );
}

export default Footer;
