import { useDispatch } from 'react-redux';
import { deleteTariff } from '../redux/slices/comparisonSlice'
import { Link } from 'react-router-dom';

import logoMts from '../assets/card/mts-logo.svg';
import logoRos from '../assets/card/ros-logo.svg';

function CardComparison({
   id,
   provider_id,
   name_promo,
   name,
   internet,
   tv_hannels,
   tv_hd_channels,
   sim_minutes,
   sim_internet,
   sim_sms,
   basic_price,
   promo_price,
   connection,
}) {

   const dispatch = useDispatch()
   const removeTariffFromComparison = () => {
      dispatch(deleteTariff(id))
   }
   return (
      <div className="cs__tariffs-item">
         <div className="cs__tariffs-item-row">
            <h3 className="cs__tariffs-item-logo">
               <img src={provider_id === "12" ? logoMts : logoRos} alt="logo" />
            </h3>
            <h3 className="cs__tariffs-item-title">{name}</h3>
            <button className="cs__tariffs-item-remove" onClick={removeTariffFromComparison}>  </button>
         </div>
         <p className="cs__tariffs-item-row">{basic_price} ./.</p>
         <p className="cs__tariffs-item-row">
            {name_promo ? name_promo : "__"}
         </p>
         <p className="cs__tariffs-item-row">
            {promo_price ? <span className="cs__tariffs-item-sale">{`${promo_price} ./.`}</span> : "___"}
         </p>
         <p className="cs__tariffs-item-row">{internet ? `${internet} /` : "___"} </p>
         <p className="cs__tariffs-item-row">{tv_hannels ? `${tv_hannels} .` : "___"} </p>
         <p className="cs__tariffs-item-row">{tv_hd_channels ? `${tv_hd_channels} .` : "___"}</p>
         <p className="cs__tariffs-item-row">{sim_internet ? `${sim_internet} ` : "___"}</p>
         <p className="cs__tariffs-item-row">{sim_minutes ? `${sim_minutes} ` : "___"}</p>
         <p className="cs__tariffs-item-row">{sim_sms ? `${sim_sms} .` : "___"}</p>
         <p className="cs__tariffs-item-row">{connection === "free" ? `` : `${connection} .`}</p>
         <p className="cs__tariffs-item-row">___</p>
         <p className="cs__tariffs-item-row">___</p>
         <p className="cs__tariffs-item-row">
            <Link to={`/tariff/${id}`}>...</Link>
         </p>
      </div>
   );
}

export default CardComparison;
